body {
  background: rgb(214, 211, 211);
  display: flex;
  align-items: center;
  justify-content: center;
}

.temp {
  border-radius: 10px;
  opacity: 0.9;
  box-shadow: 3px 3px 30px black;
  position: relative;
}
.tempCold,
.tempNormal,
.tempHot,
.tempExtraHot {
  background-color: rgb(51, 199, 199);
  transition: background-color 1s;
}

.searchCity {
  width: 90%;
  height: 50px;
  margin: 0%;
  font-size: larger;
  background: none;
  top: 10%;
  border: 1px solid whitesmoke;
  border-radius: 10px 0px 0px 10px;
  color: white;
}
.searchCity:focus {
  outline: none;
}

/*  */

.after {
  width: 100%;
  height: 100%;
  position: absolute;
  background: linear-gradient(
    rgba(0, 0, 0, 0.661),
    rgba(0, 0, 0, 0.383),
    rgba(0, 0, 0, 0.355)
  );
  border-radius: 10px;
  z-index: -1;
}
